import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { RegisterPage } from '../register/register';
import { HomePage } from '../home/home';
import { AngularFireAuth } from 'angularfire2/auth';
import { ParkingPage } from '../parking/parking';
//import {  } from '../notification/notification';
import { NgModule } from '@angular/core';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  @ViewChild('Email') Email;
  @ViewChild('password') password;
constructor(public Loauth: AngularFireAuth,public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }
  Fsiginin()
  {
    let alert = this.alertCtrl.create({
      title: 'Login',
      subTitle: 'this admin',
      buttons: ['ok']
    });
    alert.present();
  }
  signin()
  {
    /** 
    if(this.user.value =="admin" && this.password.value=="admin")
    {
      let alert = this.alertCtrl.create({
        title: 'Sign in',
        subTitle: 'admin',  
        buttons: ['ok']
      });
      alert.present();
    }
    else
    {
      let alert = this.alertCtrl.create({
        title: 'Sign in',
        subTitle: 'not admin',  
        buttons: ['ok']
      });
      alert.present();
    }*/
    
  this.Loauth.auth.signInWithEmailAndPassword(this.Email.value,this.password.value)
  .then(data =>{
    console.log("User Registered succfully",data);
    let alert = this.alertCtrl.create({
      title: 'Sign in',
      subTitle: 'User SignIN succfully',  
      buttons: ['ok']
    });
    alert.present();
    this.Loauth.authState.subscribe(ref=>{
 
      
    })

    this.navCtrl.setRoot(HomePage);
  })
  .catch(error =>
  {

    let alert = this.alertCtrl.create({
      title: 'Sign in',
      subTitle: 'Email ro password not math',
      buttons: ['ok']
    });
    alert.present();
  
  })
 
  this.navCtrl.setRoot(HomePage);
  }
  
  regis()
  {
    this.navCtrl.push(RegisterPage);
  }
  Glogin()
  {
    this.navCtrl.push(ParkingPage);
  }
}



