import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AlertController } from 'ionic-angular'; 


import { AngularFireDatabase,AngularFireAction } from 'angularfire2/database';
import { AngularFireList } from 'angularfire2/database/interfaces';
import { Observable } from 'rxjs/Observable';
 
/**
 * Generated class for the NotificationPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notification',
  templateUrl: 'notification.html',
})
export class NotificationPage {
  userRef: AngularFireList<any>;
  user: Observable<any[]>;
  itemsRef: AngularFireList<any>;
  items: Observable<any[]>;
 
  constructor(private db: AngularFireDatabase,public navCtrl: NavController, public navParams: NavParams,public alertCtrl: AlertController) {

   
      this.itemsRef = db.list('Parking');
      // Use snapshotChanges().map() to store the key
      this.items = this.itemsRef.snapshotChanges().map(data=>{
       return data.map(data =>({key:  data.payload.key, ...data.payload.val()}));
       
      });

    }
    notify(event)
    {
       console.log(event.checked);   
    }
    datachanged(key: string,event){
    
      console.log(event.checked);
      console.log(key);
      
     this.itemsRef.update(key, { Notification: event.checked });
  }
    updateItem(key: string, newText: boolean) {
      console.log(newText);
      this.itemsRef.update(key, { Notification: newText });
    }
  ionViewDidLoad() {

    console.log('ionViewDidLoad NotificationPage');
    
}

}



