import { Component, ViewChild } from '@angular/core';
import { Platform } from 'ionic-angular';
import { NavController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
 
import { MycarPage } from '../mycar/mycar'
import { NavigatePage } from '../navigate/navigate';
import { RegisterPage } from '../register/register';
import { LoginPage } from '../login/login';
import { ParkingPage } from '../parking/parking'
import { NotificationPage } from '../notification/notification';
import { Slides } from 'ionic-angular';
import { TimelinePage } from '../timeline/timeline';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase, AngularFireObject } from 'angularfire2/database';
import { Profileuser, Userdatas } from '../../model/parking/parking.model';
import { Observable } from 'rxjs/Observable';
import { AngularFireList } from 'angularfire2/database/interfaces';
 
import { User } from 'firebase/app';
import { Subscription } from 'ionic-native/node_modules/rxjs/Subscription';
import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';
import { NavParams } from 'ionic-angular/navigation/nav-params';
import { ToastController } from 'ionic-angular/components/toast/toast-controller';
import { Firebase } from 'ionic-native';
import { LaunchnavigatePage } from '../launchnavigate/launchnavigate';
import { LaunchNavigator, LaunchNavigatorOptions } from '@ionic-native/launch-navigator';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  

  _arraData :Observable<User>;
  private  userdata: AngularFireObject<Profileuser>;
   users:Observable<Profileuser[]>;
   public itemsRef;
   userP:any[];
  pet: string = "puppies";
  isAndroid: boolean = true;
  @ViewChild('username') user;
  @ViewChild('passowrd') password;
  @ViewChild(Slides) slides: Slides;
  constructor(private Platforms:Platform,private launchNavigator: LaunchNavigator,private tosat:ToastController,private nav:NavParams,public navCtrl: NavController,platform: Platform,public alertCtrl: AlertController
    ,private homeaut:AngularFireAuth,private firebaseservice:FirebaseServiceProvider,private afb:AngularFireDatabase) {
    this.isAndroid = platform.is('android');
    this.homeaut.authState.subscribe(data=>
      {
    
       this.itemsRef = this.afb.list<Profileuser>(`/ProfileUser//${ data.uid }`);
       console.log("path",this.afb.list<Profileuser>(`/ProfileUser//${ data.uid }`));
       this.users = this.itemsRef
       .snapshotChanges()
       .map
       (changes =>{
        return changes.map(data =>(
          {
            key:  data.payload.key, ...data.payload.val()
          }));
      });
    
       this.tosat.create({
         message:`Welcom to my App,${data.email}`,
         duration:3000
       }).present();
       
      
   //    this.users =  this.afb.list<Profileuser>(`/ProfileUser/${this._arraData}`).valueChanges();
      });
  
  
  
  }
  goToSlide() {
    this.slides.slideTo(2, 500);
  }
  Flogin()
  {
    let alert = this.alertCtrl.create({
      title: 'Login Facebook',
      subTitle: '10% of battery remaining',
      buttons: ['Dismiss']
    });
    alert.present();
  }
  Notification()
  {
    this.navCtrl.push(NotificationPage);
  }
  Logout()
  {
    this.navCtrl.push(LoginPage);
  }
  login()
  {
   if(this.user.value == "admin" && this.password.value =="admin")
   {
    let alert = this.alertCtrl.create({
      title: 'Login',
      subTitle: 'this admin',
      buttons: ['ok']
    });
    alert.present();
    this.navCtrl.push(RegisterPage);
   }
 
   else
   {
    let alert = this.alertCtrl.create({
      title: 'Login',
      subTitle: 'this user',
      buttons: ['ok']
    });
    alert.present();
    this.navCtrl.push(RegisterPage);
   }
  }
  GoPark()
  {
    this.navCtrl.push(ParkingPage);
  }
  Signout()
  {
    this.navCtrl.push(LoginPage);
  }
  gTimeline()
  {
    this.navCtrl.push(TimelinePage);


  }
  goMycarpage()
  {
    this.navCtrl.push(MycarPage);
  }

  goNavigatePage()
  {
 
    this.Platforms.ready().then(()=>{
   
      this.launchNavigator.navigate('18.81147,98.9542387')
        .then(
          success =>
          {
            let alert = this.alertCtrl.create({
              title: 'Launched',
              subTitle: 'Launched navigator',
              buttons: ['ok']
            });
            alert.present();
       
           },
          error =>  {
            let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Launched navigator'+error,
              buttons: ['ok']
            });
            alert.present();
             
           }
        );
      
    
      });
    
 
  }
  ionViewWillLoad() {
     
    
  }
}



