 
import { Injectable } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Parking, Profileuser, Timeline } from '../../model/parking/parking.model';
import { AngularFireAuth } from 'angularfire2/auth';
import { Subscription } from 'rxjs';
 
 
 
/*
  Generated class for the FirebaseServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FirebaseServiceProvider {

  private parkinglistRef = this.db.list<Parking>('/Parking-list/');
  private timelinelistRef = this.db.list<Timeline>('Timeline-list');
    private users;
    private userd1:Subscription[];
  
  constructor(private db:AngularFireDatabase,private auth:AngularFireAuth) {
    this.auth.authState.subscribe(data=>
    {
      this.users = this.db.list<Profileuser>(`ProfileUser/${data.uid}//`);
    
    })
  }

  getParkinglist(){
    return this.parkinglistRef;
}
getuser()
{
  return this.userd1;
}

updategps(user:Profileuser)
{
  return this.users.update(user.key, user);
}
getTimelinelist()
{
  return this.timelinelistRef;
}

}



